<template>
    <div>
        <raw-loader :mytxt="require('!raw-loader!./Tabulator2LocalAjax.vue').default"></raw-loader>
        {{ msg }}<br>
        <!-- {{myvalid}}<br> -->
        <!-- rowedit : {{rowedit}}<br>   -->
        sel : {{ sel }}<br>
        {{ tableData }}
        <div class="mb-1">
            <button class="mr-1" :disabled="cmd != '' || Object.keys(sel).length != 0" @click="onAdd">Add</button>
            <button class="mr-1" @click="ReLoadData">ReLoad Data</button>
        </div>
        <!-- {{tableData}} -->
        <div ref="table"></div>
        <b-modal ref="my-modal2" id="modal-1" size="sm" :no-close-on-backdrop="true" hide-footer title="BootstrapVue">
            <form @submit.stop.prevent="onSaveModal">
                <div>
                    <!-- <pre>{{r}}</pre> -->
                    <div class="form-group row">
                        <label class="col-sm-4 col-form-label">Id</label>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" disabled v-model="r.id" />
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-4 col-form-label">
                            Name
                        </label>
                        <div class="col-sm-8">
                            <input type="text" v-model="r.name" name="name" v-validate="'required'" :class="{
                                'form-control': true,
                                'ko-hop-le': errors.has('name'),
                            }" autocomplete="off" />
                        </div>
                    </div>

                    <div class="form-group row">
                        <label class="col-sm-4 col-form-label">
                            Ngay
                        </label>
                        <div class="col-sm-8">
                            <b-form-datepicker v-model="r.ngay" name="ngay" v-validate="'required'"
                                :date-format-options="{ year: 'numeric', month: 'numeric', day: 'numeric' }" locale="vi"
                                class="mb-2">
                            </b-form-datepicker>
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-4 col-form-label">
                            Act
                        </label>
                        <div class="col-sm-8">
                            <b-form-checkbox v-model="r.act" name="checkbox-1" value=true unchecked-value=false
                                @change="ChangeAct">
                                Active
                            </b-form-checkbox>
                        </div>
                    </div>

                    <div class="form-group row">
                        <label class="col-sm-4 col-form-label">
                            Thành phố {{ errors.has('tp') }}
                        </label>
                        <div class="col-sm-8">
                            <v-select v-model="r.tp" name="tp" v-validate="'required'" :reduce="(option) => option.id"
                                :options="tps" :has-error="errors.has('tp')" :class="{ danger: errors.has('tp') }" />
                        </div>
                    </div>
                    <div class="form-group row">
                        <label class="col-sm-4 col-form-label">
                            Quyền
                        </label>
                        <div class="col-sm-8">
                            <v-select multiple name="quyen" v-model="r.quyen" :options="['Admin', 'User', 'Editor']"
                                v-validate="'required'" :class="{ danger: errors.has('quyen') }" />
                        </div>
                    </div>

                </div>
                <div class="float-right">
                    <button type="submit" class="btn btn-primary mr-1 btn-sm">Save</button>
                    <button type="button" class="btn btn-sm" @click="hideModal()">Cancel</button>
                </div>
            </form>
        </b-modal>
    </div>
</template>
<script>
import { TabulatorFull as Tabulator } from 'tabulator-tables'; //import Tabulator library
//import $ from "jquery"
import MyLoader from "@/func/MyLoad.js";

import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate, {
    classes: true,
});

import vSelect from "vue-select";
Vue.component("v-select", vSelect);
import "vue-select/dist/vue-select.css";
import moment from 'moment';
import axios from 'axios';
export default {
    data: function () {
        return {
            tabulator: null, //variable to hold your table
            tableData: [], //data for table to display
            msg: "hello world",
            count: 1,
            r: {
                id: 0,
                ten: 'thuc 101',
                ngay: new Date
            },
            rec: {
                id: 0,
                Name: "thuc 101",
            },
            sel: {},//lưu giá trị cũ
            cmd: "",

            tps: [
                { label: 'Huế', id: 'HUE' },
                { label: 'Sài gòn', id: 'SG' },
                { label: 'Đà nẵng', id: 'DN' },
                { label: 'Quảng ngãi', id: 'QN' },
            ],
            mons: [
                { id: 1, name: 'Gạo' },
                { id: 2, name: 'Đường' },
                { id: 3, name: 'Muối' },
                { id: 4, name: 'Bột ngọt' },
                { id: 5, name: 'Nước ngọt' },
                { id: 6, name: 'Cà phê' },
                { id: 7, name: 'Bia' },
                { id: 8, name: 'Nước mía' }
            ]
        }
    },
    mounted() {
        if (!document.getElementById("myScript")) {
            MyLoader.loadScript("myScript", "/js/jquery.min.js").then(() => {
                MyLoader.loadScript("", "/js/bootstrap.js").then(() => {
                    MyLoader.loadScript("", "/js/select2.min.js").then(() => {
                        MyLoader.loadScript("", "/js/bootstrap-datepicker.js").then(() => {
                            MyLoader.loadScript("", "/js/moment.js").then(() => {
                                MyLoader.loadCss("/css/select2.min.css").then(() => {
                                    MyLoader.loadCss("/css/bootstrap-datepicker.css").then(() => {
                                        this.onReady();
                                    })
                                })
                            })
                        })
                    })
                })
            })
        } else {
            this.onReady();
        }
    },
    methods: {
        onReady() {
            this.loadTabulator();
        },
        loadTabulator() {
            var that = this;

            var tplFormatter = function (cell, formatterParams, onRendered) {
                onRendered(function () {
                    /*
                    let $input = $(cell.getElement()).find("input");
                    $input.css({
                        "padding": "4px",
                        "width": "100%",
                        "box-sizing": "border-box",
                        "font-family": "verdana",
                        "font-size": "9px"
                    })
                    */
                });
                var $el = $("<input class='form-control form-control-sm' />");
                $el.css({
                    "padding": "4px",
                    "width": "100%",
                    "box-sizing": "border-box",
                    "font-family": "verdana",
                    "font-size": "9px"
                })
                $el.val(cell.getValue());
                //let editable = (that.sel.id == cell.getRow().getData().id);
                let editable = (that.sel.id == cell.getRow().getData().id || cell.getRow().getData().id == 0);
                if (editable == false) {
                    $el.attr('disabled', 'disabled');
                }
                $el.on("change", function (e) {
                    cell.setValue($(this).val());
                });
                $el.on("blur", function (e) {
                    cell.setValue($(this).val());
                });
                return $el[0];
            };
            var cmdFormatter = function (cell, formatterParams, onRendered) {

                onRendered(function () {
                    let id = cell.getRow().getIndex();
                    $(cell.getElement()).find("#testedit").on("click", cell, that.onTestEdit);
                    //$(cell.getElement()).find("#doAdd").on("click", cell.getRow(), that.onDoAdd);
                    $(cell.getElement()).find("#doAdd").on("click", cell.getRow(), that.onSave);
                    $(cell.getElement()).find("#cancelAdd").on("click", cell.getRow(), that.onCancelAdd);
                    $(cell.getElement()).find("#save").on("click", cell.getRow(), that.onSave);
                    $(cell.getElement()).find("#cancel").on("click", cell.getRow(), that.onCancel);
                    $(cell.getElement()).find("#edit").on("click", cell.getRow(), that.onEdit);
                    $(cell.getElement()).find("#del").on("click", cell.getRow(), that.onDel);
                });

                let $div = $("<div>");
                let $btnDoAdd = $("<button>", { text: "Add", class: "mr-1" }).attr("id", "doAdd").hide();
                let $btnCancelAdd = $("<button>", { text: "Cancel", class: "mr-1" }).attr("id", "cancelAdd").hide();
                let $btnSave = $("<button>", { text: "Save", class: "mr-1" }).attr("id", "save").hide();
                let $btnCancel = $("<button>", { text: "Cancel", class: "mr-1" }).attr("id", "cancel").hide();
                let $btnEdit = $("<button>", { text: "Edit", class: "mr-1" }).attr("id", "edit");
                let $btnDel = $("<button>", { text: "Del", class: "mr-1" }).attr("id", "del");
                $div.append($btnDoAdd);
                $div.append($btnCancelAdd);
                $div.append($btnSave);
                $div.append($btnCancel);
                $div.append($btnEdit);
                $div.append($btnDel);
                let id = cell.getRow().getIndex();
                //alert(that.cmd);
                if (that.cmd == "") {
                    if (that.sel.id == cell.getRow().getData().id) {
                        $btnSave.show();
                        $btnCancel.show();
                        $btnEdit.hide();
                        $btnDel.hide();
                    } else {
                        if ($.isEmptyObject(that.sel) == true) {
                            $btnSave.hide();
                            $btnCancel.hide();
                            if (id == 0) {
                                $btnEdit.hide();
                                $btnDel.hide();
                                $btnDoAdd.show();
                                $btnCancelAdd.show();
                            } else {
                                //alert(that.cmd);
                                //$btnEdit.show();
                                // $btnEdit.attr('disabled', 'disabled');//.hide();
                                // $btnDel.attr('disabled', 'disabled');//.hide();
                            }
                        } else {
                            $btnEdit.attr('disabled', 'disabled');//.hide();
                            $btnDel.attr('disabled', 'disabled');//.hide();
                        }
                    }
                } else {//that.cmd!=""
                    if (id == 0) {
                        $btnEdit.hide();
                        $btnDel.hide();
                        $btnDoAdd.show();
                        $btnCancelAdd.show();
                    } else {
                        $btnEdit.attr('disabled', 'disabled');//.hide();
                        $btnDel.attr('disabled', 'disabled');//.hide();
                    }
                }
                return $div[0];
            };
            var datePickerFormatter = function (cell, formatterParams, onRendered) {
                onRendered(function () {
                    let $cell = $(cell.getElement()).find("input");
                    let editable = (that.sel.id == cell.getRow().getData().id || cell.getRow().getData().id == 0);
                    if (editable == false) {
                        $(cell.getElement()).find("input:first").prop("disabled", true);
                        $(cell.getElement()).find("input:first").css("background-color", "#e9ecef");
                    } else {
                        $(cell.getElement()).find("input:first").css("background-color", "white");
                    }
                    $cell.css({
                        "padding": "4px",
                        "width": "100%",
                        "box-sizing": "border-box",
                        "height": "100%",
                        //"border":"1px solid blue",
                        //"background-color":"yellow",
                        //text-align: center;
                        "text-align": "center"
                    })
                    $cell.prop("readonly", true);
                    $cell.datepicker({
                        format: "dd/mm/yyyy",
                        todayBtn: true,
                        autoclose: true,
                        todayHighlight: true,
                    });
                    let value = moment(cell.getValue(), "DD/MM/YYYY").format("DD/MM/YYYY");
                    $cell.datepicker("setDate", value);
                    $cell.datepicker().on("changeDate", function () {
                        let d = $(this).datepicker("getDate");
                        cell.setValue(moment(d).format("DD/MM/YYYY"));
                    });
                });
                var $el = $("<input class='form-control' />");
                return $el[0];
            };
            var select2Formatter = function (cell, formatterParams, onRendered) {
                onRendered(function () {
                    let $myCell = $(cell.getElement());
                    let $cell = $(cell.getElement()).find("select");
                    $cell.css({
                        "padding": "4px",
                        "width": "100%",
                        "box-sizing": "border-box",
                        "font-family": "",
                    })
                    $cell.select2({
                        placeholder: '',
                        width: "100%",
                        allowClear: true,
                    });
                    $cell.focus();
                    $cell.val(cell.getValue()).trigger("change.select2");
                    $(cell.getElement())
                        .find("select")
                        .on("change", function () {
                            cell.setValue($(this).val());
                        });
                    $myCell
                        .find(".select2-container--default")
                        .css("border", "0px solid blue");
                    $myCell
                        .find(".select2-selection--single")
                        .css("border", "0px solid blue");
                });
                var $el = $("<select class='tabu' >");
                //let editable = (that.sel.id == cell.getRow().getData().id);
                let editable = (that.sel.id == cell.getRow().getData().id || cell.getRow().getData().id == 0);
                if (editable == false) {
                    $el.attr('disabled', 'disabled');
                } else {
                }
                formatterParams.forEach((el) => {
                    let $option = $("<option>").attr("value", el.id).text(el.text);
                    $el.append($option);
                });
                return $el[0];
            };
            var roweditCheck = function (cell) {
                let row = cell.getRow();
                let id = row.getIndex();
                let $cell = row.getElement();
                if (that.sel.id == cell.getRow().getData().id) {
                    return true;
                } else {
                    if (id == 0) {
                        return true;
                    } else {
                        return false;
                    }

                }
            }
            this.tabulator = new Tabulator(this.$refs.table, {

                columnVertAlign: "middle",//"middle", //align header contents
                data: this.tableData, //link data to table
                reactiveData: true, //enable data reactivity
                height: $(window).innerHeight() - 400,//'200px',//$(window).innerHeight()
                history: true,             //allow undo and redo actions on the table
                pagination: "local",       //paginate the data
                paginationSize: 2,         //allow 7 rows per page of data
                paginationCounter: "rows", //display count of paginated rows in footer
                paginationInitialPage: 1, //optional parameter to set the initial page to load    
                paginationSizeSelector: [5, 10, 20, 30, 50],
                debugInvalidOptions: false,                



                rowFormatter: function (row) {
                    let chk = that.sel.id == row.getData().id;
                },
                index: "id",
                //autoColumns:true,
                columns: [
                    {
                        title: "STT",
                        formatter: "rownum",
                        headerHozAlign: "center",
                        hozAlign: "center",
                        vertAlign: "middle",//"bottom",
                    },
                    {
                        title: "Cmd",
                        headerHozAlign: "center",
                        hozAlign: "center",
                        vertAlign: "middle",//"bottom",
                        headerSort: false,
                        width: 100,
                        formatter:cmdFormatter,
                    },
                    {
                        title: "Id",
                        field: "id",
                        width: 50,
                        vertAlign: "middle",//"bottom",
                        headerFilter: "input",
                        hozAlign: "center",
                    },
                    {
                        title: "Name",
                        field: "Name",
                        width: 120,
                        vertAlign: "middle",//"bottom",
                        headerFilter: "input",
                        editor: "input",
                        validator: ["required"], //"required"  
                        editable: roweditCheck,//editCheck,
                    },

            //         title:"Name", 
            // field:"Name", 
            // //headerFilter: true,
            // headerFilter:"input",
            // width:200,
            // headerHozAlign: "center",
            // hozAlign: "left",




                ], //define table columns
            });
            this.tabulator.on("dataLoaded", function (data) {
                that.onLoad();
            });
            this.tabulator.on("pageLoaded", function (pageno) {
            });
            this.tabulator.on("dataChanged", function (data) {
            });
            this.tabulator.on("rowClick", function (e, row) {
            });
            this.tabulator.on("rowSelectionChanged", function (data, rows) {
            });
            this.tabulator.on("rowSelected", function (row) {
                let index = row.getIndex();
                //that.msg = index;
                //console.log('rowSelected');
            });
            this.tabulator.on("cellEditing", function (cell) {
                let row = cell.getRow();
                let data = row.getData();
                let index = row.getIndex();
            });
            this.tabulator.on("cellEditCancelled", function (cell) {
            });
            this.tabulator.on("cellEdited", function (cell) {
                if (cell.getField() != "solg") {
                    return;
                }
                let row = cell.getRow();
                let data = row.getData();
                let solg = row.getCell("solg").getValue();
                let tien = solg * 1000;
                let id = row.getIndex();
                row.getCell("tien").setValue(tien);
            });
            this.tabulator.on("validationFailed", function (cell, value, validators) {
                let field = cell.getField();
            });
        },
        ReLoadData() {
            this.tabulatorClear();
            let r = JSON.parse(JSON.stringify(this.rec));
            r.id = this.count++;
            this.tableData.push(r);
            this.count++;
            this.setPage(1);
        },
        onLoad() {
            /*
            for (var i = 0; i < 3; i++) {
                let r = JSON.parse(JSON.stringify(this.rec));
                r.id = this.count;
                r.tien = r.solg * 1000;
                this.tableData.push(r);
                this.count++;
            }
            this.setPage(1);
            */

            this.GetAll().then(res => {
                this.tabulatorClear();
                res.data.forEach(x => {
                    this.tableData.push({
                        id: x.Id,
                        Name: x.Name
                    })
                })
                this.setPage(1);
            })



        },
        ChangeAct() {
            let act = (this.r.act == 'true') ? true : false;
            this.r.act = act;
        },
        rowValidate(row) {
            let kq = true;
            var cells = row.getCells();
            for (var i = 0; i < cells.length; i++) {
                if (cells[i].isValid() != true) {
                    kq = false;
                }
            }
            return kq;
        },
        onSave(e) {
            e.preventDefault();
            let row = e.data;
            var cells = row.getCells();
            let id = row.getIndex();
            let index = this.tableData.findIndex(x => x.id == id);
            if (
                this.rowValidate(e.data) == false
                //|| row.getData().tpl == ""
                //|| row.getData().monid == null
            ) {
                this.toast("không hợp lệ!!!")
                return;
            }
            let r = JSON.parse(JSON.stringify(row.getData()));
            if(r.id==0){
                this.Save(r).then(res=>{
                    this.tabulatorAdd({
                        id:res.data.rec.Id,
                        Name:res.data.rec.Name
                    });
                    this.onCancelAdd(e);
                })
            }else{
                this.Save(r).then(res=>{
                    // alert(JSON.stringify(res));
                    // {"data":{"kq":"updateok","msg":"Update bàn ok"},"st
                    this.tabulator.deselectRow(this.sel.id);
                    this.sel = {};
                    this.tabulator.redraw(true);
                })
                /*
                this.tabulator.deselectRow(this.sel.id);
                this.sel = {};
                this.tabulator.redraw(true);
                */
            }
        },
        onSaveModal() {
            this.$validator.validateAll().then((result) => {
                if (!result) {
                    this.toast("Không hợp lệ");
                    return;
                }
                let r = JSON.parse(JSON.stringify(this.r));
                if (r.id == 0) {
                    r.id = this.count;
                    this.tabulatorAdd(r);
                    this.count++;
                    this.setPage(1);
                } else {
                    this.tabulatorUpdate(r);
                }
                this.hideModal();
            });
        },
        onAdd() {
            this.cmd = "add";
            let r = JSON.parse(JSON.stringify(this.rec));
            this.tabulator.addRow(r, true, 0);
            this.tabulator.selectRow(0);
            $(".tabulator-footer").hide();
            this.tabulator.redraw(true)
        },
        onCancelAdd(e) {
            e.preventDefault();
            let row = e.data;
            var cells = row.getCells();
            let id = row.getIndex();

            this.cmd = "";
            row.delete();
            $(".tabulator-footer").show();
            this.tabulator.redraw(true)
        },
        onCancel(e) {
            e.preventDefault();
            let row = e.data;
            var cells = row.getCells();
            let id = row.getIndex();
            let index = this.tableData.findIndex(x => x.id == id);
            let $row = $(row.getElement());
            let r = JSON.parse(JSON.stringify(this.sel));
            
            this.tabulatorUpdate(r);
            this.sel = {};
            //this.tabulator.deselectRow(this.sel.id);
            this.tabulator.redraw(true);
        },
        onEdit(e) {
            e.preventDefault();
            let row = e.data;
            var cells = row.getCells();
            let id = row.getIndex();
            let index = this.tableData.findIndex(x => x.id == id);
            this.sel = JSON.parse(JSON.stringify(row.getData()));
            let $row = $(row.getElement());
            this.tabulator.selectRow(id);
            this.tabulator.redraw(true)
        },
        onDel(e) {
            e.preventDefault();
            let row = e.data;
            let id = row.getIndex();
            //alert(`del ${id}`);
            //return;
            this.Del(id).then(res=>{
                // alert(JSON.stringify(res));
                // {"data":{"kq":"ok","msg":"Delete bàn ok"},"st
                if(res.data.kq=="ok"){
                    this.tabulatorDel(id);
                }
                this.toast(res.data.msg);
            })
            //this.tabulatorDel(id);
        },
        getPageMax() {
            return this.tabulator.getPageMax();
        },
        getLastPage(count) {
            let pageSize = this.tabulator.getPageSize();
            let du = count % pageSize;
            return du == 0 ? count / pageSize : (count - du) / pageSize + 1;
        },
        getPageSize() {
            return this.tabulator.getPageSize();
        },
        getPage() {
            return this.tabulator.getPage();
        },
        setPage(_page) {
            this.tabulator.setPage(_page);
        },
        getData() {
            return this.tabulator.getData();
        },
        tabulatorLoadData(ar) {
            ar.forEach(x => {
                this.tableData.push(x);
            })
        },
        tabulatorClear() {
            let i = 0;
            while (i < this.tableData.length) {
                this.tableData.splice(i, 1);
            }
        },
        tabulatorAdd(r) {
            this.tableData.unshift(r);
        },
        tabulatorDel(id) {
            let index = this.tableData.findIndex(x => x.id == id);
            this.tableData.splice(index, 1);
        },
        tabulatorUpdate(r) {
            let index = this.tableData.findIndex(x => x.id == r.id);
            this.tableData.splice(index, 1, r);
        },
        tabulatorGetRow(id) {
            //let index=this.tabulator.getData().findIndex(x=>x.id==id);
            //let r=this.tabulator.getData()[index];
            //return r;
        },
        toast(msg, variant = null) {
            this.$bvToast.toast(msg, {
                title: `Information`,
                variant: variant,
                solid: true,
            });
        },
        showModal() {
            this.$refs['my-modal2'].show()
        },
        hideModal() {
            this.$refs['my-modal2'].hide()
        },

        async GetAll() {
            return await axios.get(`/api/Ban/GetAll`).catch((err) => {
                console.log(err);
            });
        },
        async Save(r) {
            return await axios.post(`/api/Ban/Save`, r).catch((err) => {
                console.log(err);
            });
        },
        async Del(id) {
            return await axios.get(`/api/Ban/Delete?id=${id}`).catch((err) => {
                console.log(err);
            });
        },



    }
}
</script>
<style lang="scss">
.select2-container--default .select2-selection--single .select2-selection__arrow {
    height: 15px;
    position: absolute;
    top: 4px;
    right: 1px;
    width: 20px;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
    color: #444;
    line-height: 24px;
}

.select2-container .select2-selection--single {
    box-sizing: border-box;
    cursor: pointer;
    display: block;
    height: 24px;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-user-select: none;
}

.xyz {
    border: 1px solid red;
}

.danger,
.danger .dropdown-toggle,
.danger .selected-tag {
    background-color: aqua;
    border-radius: 4px;
